/**
 * 工具方法
 * @author jero
 * @date 2016-08-01
 */

let scrollBarWidth = 0;

const scrollbarMeasure = {
  position: 'absolute',
  top: '-9999px',
  width: '50px',
  height: '50px',
  overflow: 'scroll',
};

function getScrollBarWidth() {
  if (scrollBarWidth) {
    return scrollBarWidth;
  }

  const scrollDiv = document.createElement('div');

  Object.keys(scrollbarMeasure).forEach((k) => {
    scrollDiv.style[k] = scrollbarMeasure[k];
  });

  document.body.appendChild(scrollDiv);

  const sWidth = scrollDiv.offsetWidth - scrollDiv.clientWidth;

  document.body.removeChild(scrollDiv);
  scrollBarWidth = sWidth;

  return sWidth;
}

function getCurrentStyle(elem) {
  return window.getComputedStyle ? window.getComputedStyle(elem) : elem.currentStyle;
}

function getExtraWidth(elem) {
  const style = getCurrentStyle(elem);

  return parseInt(style.marginLeft.replace('px', ''), 10) +
          parseInt(style.marginRight.replace('px', ''), 10);
}

function checkScrollbar() {
  const body = document.body;
  let fullWindowWidth = window.innerWidth;

  if (!fullWindowWidth) { // workaround for missing window.innerWidth in IE8
    const documentElementRect = document.documentElement.getBoundingClientRect();
    fullWindowWidth = documentElementRect.right - Math.abs(documentElementRect.left);
  }

  const extraWidth = getExtraWidth(body);

  return (body.clientWidth + extraWidth) < fullWindowWidth;
}

export { getScrollBarWidth, checkScrollbar };
